
.bkg-main {
  width: 100%;
  height: 505px;
  overflow: hidden;
}
.bkg {
  /* width: 100%; */
  height: 505px;
  background-image: url(/static/img/background.png);
  border: 3px solid green;
  position: absolute;
}
.bird {
  position: absolute;
  z-index: 20;
  width: 33px;
  height: 24px;

  -webkit-transition: -webkit-transform 0.5s;

  transition: -webkit-transform 0.5s;

  transition: transform 0.5s;

  transition: transform 0.5s, -webkit-transform 0.5s;
}
.bird-0 {
    background: url(/static/img/bird1.png) 50% 50% no-repeat;
}
.bird-1 {
    background: url(/static/img/bird2.png) 50% 50% no-repeat;
}
.bird-2 {
    background: url(/static/img/bird3.png) 50% 50% no-repeat;
}
.pipe-group {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  z-index: 20;
  overflow: hidden;
}
.pipe-group li {
    text-decoration: none;
}
.pipe {
  width: 100px;

  position: absolute;
}
.pipe-top {
  /* background-color: red; */
  background: url(/static/img/pipe_down.png) bottom no-repeat;
  background-size: cover;
}
.pipe-bottom {
  /* background-color: red; */
  background: url(/static/img/pipe_up.png) no-repeat;
  background-size: cover;
}
.space {
  height: 20px;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.score, .score-max {
  position: absolute;
  left: 20px;
  top: 20px;
  font-size: 2em;
  font-weight: bolder;
}
.score-max {
  left: 200px;
}
